.sidebar{
  //padding-top: 30px;
  height: 100vh;
  position: fixed;
}

.name{
  margin-bottom: 0px;
}
.add{
  box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px;
  background-color: rgb(13, 110, 241);
  font-size: 12px;
  margin-left: 4px;
  height: 32px;
  color: white;
  border: transparent;
  border-radius: 2px;
  overflow: hidden;
  padding: 0px 12px;
  a{
    color: white;
    text-decoration: none;
  }
}

.compare{
  font-size: 12px;
  height: 32px;
  color: white;
  box-shadow: white 0px 0px 0px 1px inset, rgba(0, 0, 0, 0) 0px 0px 0px 0px;
  background-color: transparent;
  transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
  border: transparent;
  border-radius: 2px;
  padding: 0px 12px;
}

.menu{
  font-size: 14px;
  color: white;
  list-style-type: none;
  padding-right: 0px;
  margin-top: 20px;
  li{
    line-height: 2.5;
    cursor: pointer;
    a{
      color: rgb(173, 179, 185)!important;

      span{
        margin-left: 10px;
      }
    }

    .active{
      color: white!important;
      font-weight: bold;
    }
  }
}
