.header {
  position: relative;
  .header-overlay {
    height: 300px;
    background-image: url("../assets/img/banner.jpg");
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;

    &:before {
      height: 100%;
      content: '';
      background: #00000085;
      width: 100%;
      position: absolute;
    }


  }

  .header-info {
    position: absolute;
    top: 0;
    .breadcrumb {
      background-color: transparent;

      li {
        padding: 5px;
        color: white;

        svg {
          font-size: 12px;
        }

        &:last-child svg {
          display: none;
        }
      }
    }

    .col-info {
      font-size: 13px;
      color: rgba(218, 218, 218, 0.94);
      padding-top: 120px;
    }

    .col-details {
      font-size: 11px;
      color: white;
    }
  }
}

.gifts {
  list-style-type: none;
  padding-right: 15px;
  color: white!important;
  li {
    font-size: 12px;
    margin-bottom: 5px;
    img{
      width: 13px;
    }
  }
}

.report-result {
  .result-num {
    color: rgb(200, 67, 67);
    font-size: 27px;
  }

  span {
    color: white;
    font-size: 14px;
  }
}

.report-result-green-small{
  .result-num {
    color: rgb(126, 213, 136);
    font-size: 12px;
  }

  span {
    color: white;
    font-size: 11px;
  }
}

.report-result-green {
  .result-num {
    color: rgb(126, 213, 136);
    font-size: 27px;
  }

  span {
    color: white;
    font-size: 14px;
  }
}

.news {
  padding-left: 30px;

  .title {
    color: #ffffffcf;
    font-size: 14px;
  }

  .description {
    font-weight: bold;
    text-align: justify;
    margin-top: 15px;
    font-size: 15px;
    color: #FFFFFF;
  }
}

.btn-light-outline {
  background: transparent;
  border: 1px solid white;
  border-radius: 3px;
  color: white;
  padding: 0px 12px;
  height: 36px;
}

.scroll{
  overflow-y: scroll;
}

.element{
  width: 100%;
}

.each-portfolio{
  display: flex;
  width: 100%;
  align-items: center;
  margin-top: 15px;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(226, 226, 226);
  .portfolio-details{
    h4{
      font-size: 14px;
      color: rgb(0, 0, 0);
      text-transform: none;
      width: 100%;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin: 0px 10px 0px 0px;
      overflow: hidden;
    }

    p{
      font-size: 12px;
      color: rgb(124, 134, 143);
      width: 100%;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin: 0px 10px 0px 0px;
      overflow: hidden;
    }
  }
}

.MuiDialogTitle-root{
  h2{
    color: #1d2124;
  }
}
.new-portfolio-card{
  margin-top: 16px;
  padding: 16px 24px;
  background: rgb(244, 244, 244);

  .MuiInput-underline:before{
    border-bottom: 1px solid #000000!important;
  }

  .MuiFormLabel-root{
    color: black!important;
  }
}
.new-portfolio{
  position: relative;
  line-height: 1;
  user-select: none;
  vertical-align: top;
  font-size: 12px;
  height: 40px;
  color: black;
  display: inline-block;
  width: auto;
  box-shadow: black 0px 0px 0px 1px inset, rgba(0, 0, 0, 0) 0px 0px 0px 0px;
  background-color: transparent;
  transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
  border-width: 0px;
  border-style: initial;
  border-color: initial;
  border-image: initial;
  border-radius: 2px;
  overflow: hidden;
  padding: 0px 16px;
  text-decoration: none;
  margin-left: 4px;
}

.confirm{
  position: relative;
  line-height: 1;
  user-select: none;
  vertical-align: top;
  font-size: 12px;
  height: 40px;
  color: white;
  display: inline-block;
  width: auto;
  box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px;
  background-color: rgb(13, 110, 241);
  transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
  border-width: 0px;
  border-style: initial;
  border-color: initial;
  border-image: initial;
  border-radius: 2px;
  overflow: hidden;
  padding: 0px 16px;
  text-decoration: none;
}
